@use 'reset';

$violet : #7469F0;

@mixin flexRow {
    display :flex;
    flex-direction: row;
}

.wrap {
    width: 1280px;
    margin: 30px auto;
}

.login {
    width: 1280px;
    @include flexRow();
    justify-content: flex-end;
    // 로그인 / 회원가입 우측 상단으로 보내려고함
}

.login > a {    
    margin-right: 10px;
}

.logo {
    margin: 15px 0 0 25px;
    width: 150px;
    display: inline-block;
}

.logo > h1 {
    width: 150px;
    font-size: 30px;
    color: $violet;
    font-weight: bold;
}

// 메뉴
.nav {
    margin: 15px 0 35px 110px;
    width: 750px;
    display: inline-block;
}

.nav > ul {
    width: 700px;
    @include flexRow();
    text-align: center;
    font-size: 20px;
}

.nav > ul >li {
    margin-left: 100px;
}

// 서브메뉴
.nav .navSub {
    display: none;
    width: 120px;
    font-size: 16px;
    margin : 20px auto;
}

.nav .navSub > li {
    margin-top: 10px;
}

